/* eslint-disable css-modules/no-undef-class, css-modules/no-unused-class */
import React from 'react';
import PropTypes from 'prop-types';

import { addStyleSheet } from './DOMUtils';
import './Btn.css';
import './effects/Effects.css';
import './effects/Effects1.css';

const noop = () => null;

class Btn extends React.PureComponent {
  static propTypes = {
    children: PropTypes.node,
    txt: PropTypes.string,
    effect: PropTypes.string,
    bg: PropTypes.string,
    color: PropTypes.string,
    hoverBg: PropTypes.string,
    hoverColor: PropTypes.string,
  };

  static defaultProps = {
    children: null,
    txt: '',
    effect: '',
    bg: 'blue',
    color: 'yellow',
    hoverBg: 'black',
    hoverColor: 'white',
  };

  constructor() {
    super();
    this.removeStyleSheet = noop;
  }

  componentWillUnmount() {
    this.removeStyleSheet();
  }

  handleRef = (ref) => {
    if (!ref) return;

    const { effect } = this.props;
    const width = ref.offsetWidth;
    const height = ref.offsetHeight;
    const ratio = width / height;

    let styles;
    /* Because we can't use react to set psuedo element css */
    if (effect === '1e') {
      styles = `
        .fx {
          color: ${this.props.color};
        }
        .fx:hover::after {
          height: ${ratio * 81.76}%;
        }
        .fx:hover button {
          color: ${this.props.hoverColor};
        }
        .fx:active::after {
          height: ${ratio * 125.78}%;
        }
        .fx::after {
          background: ${this.props.hoverBg};
        }
      `;
    }
    console.info(styles);
    this.removeStyleSheet();
    this.removeStyleSheet = addStyleSheet(styles);
  };

  render() {
    const { handleRef } = this;
    const { txt, children, bg, effect, ...props } = this.props;
    const buttonClasses = ['root'];
    // e.g. effect = 1b should add cass effects and effects-1b
    if (effect.length > 0) {
      const effectNum = effect[0];
      if (!Number(effectNum) > 0) {
        throw Error(`${effect} is not a valid effect`);
      }
      buttonClasses.push('fx');
      buttonClasses.push(`fx-${effect}`);
      buttonClasses.push(`fx-${effectNum}`);
    }

    return (
      <div
        style={{ background: bg }}
        className={buttonClasses.join(' ')}
        ref={handleRef}
      >
          <button {...props} >
            {txt || children}
          </button>
      </div >
    );
  }
}

export default Btn;



